import React, { useState } from "react";
import "./navbar.scss";
import { Anchor, Drawer } from "antd";
import { FiMenu, FiX } from "react-icons/fi";

export const Navbar: React.FC = () => {
  const [visible, setVisible] = useState(false);

  const items = [
    {
      key: "about",
      href: "#about",
      title: " ",
    },
    {
      key: "advantages",
      href: "#advantages",
      title: "",
    },
    {
      key: "explain-work",
      href: "#explain-work",
      title: "  ",
    },
    {
      key: "contacts",
      href: "#contacts",
      title: "",
    },
    {
      key: "question",
      href: "#question",
      title: "",
    },
  ];

  const showDrawer = () => {
    setVisible(true);
  };

  const onClose = () => {
    setVisible(false);
  };
  return (
    <div className="container-fluid">
      <div className="header">
        <div className="logo">
          <div className="fas fa-bolt"></div>
          <a className="title-header" href="https://dev-inside.ru">
            DevInside
          </a>
        </div>
        <div className="mobileHidden">
          <Anchor targetOffset={100} items={items} />
        </div>
        <div className="mobileVisible">
          {/* <Button type="primary" onClick={showDrawer}>
            <i className="fas fa-bars"></i>
          </Button> */}
          <div className="burger" onClick={showDrawer}>
            <FiMenu size={30} />
          </div>
          
          <Drawer
            placement="right"
            closable={false}
            onClose={onClose}
            open={visible}
          >
            <div className="mobile-close" onClick={onClose}>
              <FiX size={30}/>
            </div>
            <Anchor targetOffset={120} items={items} />
          </Drawer>
        </div>
      </div>
    </div>
  );
};
